.container-contact{
    /* height: 100px; */
    /* height: calc(5px + 5vw); */
    background: var(--bar-color);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 25px rgba(255, 255, 255, 0.2);
}
.sub-container-contact{
    width: min(100%-40px , 960px);
}
.contact-icon-div{
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contact-icon-div p , .contact-icon-div a{
    color: #fff;
    text-decoration: none;
    margin: 10px 0 0 0;
    font-size: calc(2px + 1.7vmin);
}
.contact-icon{
    color: #fff;
    font-size: 40px;
    font-size: calc(5px + 4vmin);
    transition-duration: 0.5s;
}
.contact-icon:hover{
    color: orange;
}
.container-contact-phone{
    transform: translateY(5px);
}

@media only screen and (max-width: 768px){
    .contact-icon-div{
        width: 25%;
    }
}