.container-education{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--text-color);
    font-size: calc(4px + 2vmin);
}
.logo-kmitl{
    /* width: 200px; */
    width: calc(10px + 25vmin);
}
.data-educate{
    font-size: calc(1px + 2vmin);
    padding-left: 30px;
}
.sub-container-education{
    width: min(100% - 50px , 960px);

    display: flex;
    align-items: center;
    justify-content: center;
}