.container-home{
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    /* font-size: calc(4px + 2vmin); */
    color: #aaa;
    
}

.sub-container-home{
    /* width: min(100% - 50px , 1440px); */
    display: flex;
    justify-content: center;
    align-items: center;
    width: min(100% - 50px , 1040px);
    position: relative;
}

.home-code-text{
    font-size: calc(4px + 1vmin);
    z-index: 10;
    position: absolute;
    left: 0;
}
.h1-text{
    display: inline-block;
    margin: 0;
    font-size: calc(10px + 6.4vmin);
    color: var(--text-color);
    text-shadow: 5px 5px 5px var(--background);
}
.p-text{
    display: inline-block;
    margin: 0;
    color: var(--text-color);
    font-size: calc(1.4px + 2vmin);
    font-weight: bold;
    text-shadow: 3px 3px 3px var(--background);
}

.space{
    margin: 0;
    padding: 0;
}
.highlight-red{
    color: #ff7070;
}
.highlight-blue{
    color: #61dafb;
}
.highlight-green{
    color:#b8e66e;
}

.txt-img{
    color: white;
    font-size: 1.9px;
    margin-left: 4%;
    z-index: 5;
    position: absolute;
    right: 0;
    user-select: none;
}

@media only screen and (max-width: 768px){
    .display-none{
        display: none;
    }
    .home-code-text{
        left: unset;
    }
    .home-txt-shadow{
        text-shadow: 0 0 1px #aaa;
    }
}