.project-list-page{
    min-height: 92vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Project-link , .project-link-py{
    /* width: 20%; */
    width: min(100vw - 100px , 400px);
    border: 5px var(--text-color) solid;
    border-radius: 2vmin;
    color: var(--text-color);
    text-decoration: none;
    margin: 10px;
    padding: 10px;
    /* transition-duration: 0.5s; */
    transition: all 1s ease;
    font-size: calc(5px + 2vmin);
    min-height: 2vmin;
}
.Project-link:hover , .project-link-py:hover{
    box-shadow: var(--box-shadow);
}

.go-back{
    position: fixed;
    z-index: 100;
    top: 10%;
    left: 4%;
    /* right: 4%; */
    font-weight: bold;
    cursor: pointer;
    color: var(--text-color);
    font-size: calc(5px + 2vmin);
    display: inline-block;
    transition-duration: 0.3s;
    border-bottom: 3px solid var(--text-color) transparent;
    padding-bottom: 1vmin;
    /* margin: 2vmin; */
}
.go-back:hover{
    text-shadow: 0 0 30px var(--text-color);
    border-bottom: 3px solid var(--text-color);
    padding-bottom: 0;
}

.test{
    position: fixed;
    top: 10%;
    left: 4%;
}
@media only screen and (max-width: 768px){
    .test{
        top: 10%;
        /* right: 4%; */
        left: 10%;
    }
}