.container-project-desc{
    display: flex;
    align-items: center;
    justify-content: center;
}
.container-project-article{
    width: min(100% - 50px , 960px);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--text-color);
    font-size: calc(5px + 1.5vmin);
    /* font-size: calc(1.5vmin); */
}
.head-describe{
    display: flex;
    justify-content: center;
    /* margin: 5vh 0 4vh 0; */
    margin: 5vh 0 4% 0;
    cursor: default;
    user-select: none;
}
.container-text-py{
    width: min(85% - 50px , 768px);
    margin: 0;
    width: 85%;
}

.pic-bot-trade{
    width: 85%;
    box-shadow: var(--box-shadow);
    margin: 2vmin 0;
    border-radius: 3vmin;

}

@media only screen and (max-width: 768px){
    .head-describe{
        width: auto;
        padding: 10px calc(20px + 10vmin);
    }
}
