.container-blur{
    width: 90%;
    position: relative;
}
.blur-img{
    width: 100%;
}
.container-blur::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url('../../../image/BotGameWeb/Bot-game01.png');
    background-size: 100%;
    clip-path: polygon(50% 10%, 50% 95%, 98% 95%, 98% 10%);
    filter: blur(5px);
    /* border-radius: 10vmin; */
}

.container-bot-pic{
    width: 90%;
}
.pic-bot-shadow{
    box-shadow: var(--box-shadow);
    margin: 2vmin 0;
    border-radius: 3vmin;
}