.container-about{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(4px + 2vmin);
    color: var(--text-color);
    
}
.sub-container-about{
    display: flex;
    align-items: flex-start;
    justify-content: center;

    width: min(100% - 50px , 960px);
    /* width: min(100% + 10px , 960px); */
}
.data-about{
    text-align: left;
    /* padding: 0 40px; */
    padding: 0 4%;
}
.sub-other-skill{
    font-size: calc(1px + 2vmin);
    /* padding: 0 30px; */
    padding: 0 3%;
    translate: calc(5vmin) 0;
}
.head-other-skill{
    margin: 0;
}