/* Set variables CSS */
:root{
    --background:#282c34;
    /* --bar-color:#444; */
    --bar-color:#353a44;
    --box-shadow: 0 0 25px rgba(255, 255, 255, 0.4);
    --bar-box-shadow: 0 0 25px rgba(255, 255, 255, 0.2);
    --text-color: #fff;
    /* --text-color: orange; */

    /* --background:#FAF8F1; */
    /* --box-shadow: 0 0 25px rgba(33,33,33,.2); */
    /* --text-color: #C58940; */
}
.container {
    padding: 0;
    margin: 0;
    background-color: var(--background);
}

/* width */
::-webkit-scrollbar {
    width: 12px;
    background-color: var(--background);
}
/* Track */
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 6px;
}
/* Handle */
::-webkit-scrollbar-thumb {
    background: #444; 
    box-shadow: inset 0 0 5px grey; 
    border-radius: 7px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #353a44; 
    box-shadow: inset 0 0 7px grey; 
}

@media only screen and (max-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}

    .display-none {display: none;}
}