.container-project{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--text-color);
    font-size: calc(4px + 2vmin);
}

section{
    width: min(100% - 50px , 960px);
    height: 20%;
    margin: 15px 0;    
    
    display: flex;
    align-items: center;
    justify-content: center;
}
.project{
    border: 5px #aaa solid;
    border-radius: 3vmin;
    height: 100%;
    width: 50%;
    margin: 0 10px;
    transition-duration: 0.5s;
}
.project:hover {
    box-shadow: var(--box-shadow);
}
.icon-project{
    color: var(--text-color);
    font-size: calc(10px + 6vmin);
    margin-bottom: 2vmin;
}
.project-link{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: var(--text-color);
}